@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    width: 80%;
    margin: auto;
    font-size: 0.9rem;
}
@media screen and (max-width: 1200px) {
    .App {
        width: 100%;
        margin: auto;
        font-size: 0.8rem;
    }
}
.line {
    width: 50%;
    border: 1px solid #ccc;
    margin: 30px 25% 20px 25%;
}


header {
    height: 80px;
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
    box-shadow: 0px 3px 3px #ccc;
    margin: auto;
}

.header_one, .header_two, .header_three {
    display: flex;
    justify-content: flex-start;
    flex-wrap: nowrap;
    margin-left: 75px;
}
/* First header */

.header_one a img {
    width: 50px;
    height: 50px;
    margin-top: 15px;
}

/* Second header */

.header_two {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

@media screen and (max-width: 425px) {
    .header_two {
        display: none;
    }
}

.header_two a {
    padding: 10px 15px;
    text-decoration: none;
    //font-size: 24px;
    font-family: 'Dancing Script', cursive;
    color: black;
}
.header_two a:hover {
    color: #ccc;
    transition: 0.6s;
}

/* Third header */ 
.header_three {
    margin-right: 100px;
}
.header_three ul {
    padding: 0;
    display: flex;
    justify-content: flex-start;
    flex-wrap: nowrap;
}
.header_three ul li {
    list-style-type: none;
    margin-right: 12px;
    position: relative;
    top: 30%;
}

.header_three ul li a {
    text-decoration: none;
    color: #000;
    white-space: nowrap;
}

.header_three ul li a:hover {
    color: #ccc;
    transition: 0.6s;
}
main {
    background-image: url(/old_mill_site/static/media/dot-grid.5a69c256.png);
    margin-top: 5px;
}
.about {
    padding: 10px 0;
}
h3 {
    text-align: center;
}

p {
    width: 95%;
    margin: auto;
}
h2 {
    text-align: center;
}
.services {
    width: 95%;
    margin: auto;
}
.contacts {
    margin: 30px;
    display: flex;
    justify-content: space-between;
}

.contacts_person {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 49%;
    font-size: 1.2rem;
}
.contacts_person div {
    text-align: center;
}
a:last-child {
    text-decoration: none;
}
.contacts_address {
    width: 49%;
}

iframe {
    width: 100%;
    height: 70vh
}
@media screen and (max-width: 425px) {
    .contacts {
        flex-direction: column-reverse;
    }
    .contacts_address,.contacts_person {
        width: 100%;
    }
}


footer {
    height: 80px;
    display: flex;
    justify-content: space-around;
    flex-wrap: nowrap;   
    box-shadow: 0 -3px 3px #ccc;
    font-size: 0.8rem;
}
.creator {
    padding: 10px 15px;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.creator div {
    white-space: nowrap;
}
.creator a img{
    width: 40px;
    height: 40px;
}
.wish {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 0 5px 0 5px;
}

h2 {
    margin: 0;
    padding: 0;
}
.direct_contact {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 0 5px 0 0;
}
.direct_contact a {
    text-align: left;
}

@media screen and (max-width: 425px){
    footer {
        font-size: 0.4rem;
    }
    .creator a img{
        width: 25px;
        height: 25px;
    }
}
